<template>
    <div>

        <slot name="label"/>

        <div v-for="(item, index) in items">
            <slot :item="item" :index="index"/>
            <button class="repeating-form-group-delete-button" v-if="showDeleteButton" @click.prevent="items.splice(index, 1)">
                <slot class="delete-button-text">Delete</slot>
            </button>
        </div>

        <button class="repeating-form-group-add-button" v-if="showAddButton" @click.prevent="$emit('add-item')">
            <slot name="add-button-text">Add</slot>
        </button>

    </div>
</template>

<script>
    export default {
        name : "repeating-form-group",
        props: {
            items         : {},
            showAddButton   : {
                default: true
            },
            showDeleteButton: {
                default: true
            }
        }
    }
</script>


